import React, { useEffect, useState } from 'react'
import Loader from 'react-loader-spinner';

const ModalTr = ({ confirm, setConfirm, submitT, load, data, total }) => {

  const [size, setSize] = useState(200);
  const [modal, setModal] = useState('d-none');

  const resized = () => {
    if (window.innerWidth <= 600) {
      setSize(50)
    } else if (window.innerWidth <= 768) {
      setSize(100);
    } else {
      setSize(200)
    }
  }

  useEffect(() => {
    resized();
    window.addEventListener('resize', resized);
    return () => window.removeEventListener('resize', resized);
  }, []);

  useEffect(() => {
    if (confirm) {
      setModal('d-flex')
    } else {
      setModal('d-none')
    }
  }, [confirm]);

  useEffect(() => {
    if (load) {
      document.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      document.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  const numberWithCommas = (x) => {
    return x.toFixed(2).toString().replace('.', ',').replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  }

  return (
    <div id='modalTransaksi' className={"modal align-items-center justify-content-center " + modal}>

      <div className="modal-content wrap-b d-flex p-3 flex-direction-column gap-3" style={{ width: `calc(100% - ${size}px)`, height: `calc(100% - ${size}px)` }}>
        <div className="card-modal" style={{ zIndex: 1, left: '25%', right: '25%' }}>
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
            className="justify-content-center align-items-center d-flex"
            style={{
              position: 'absolute',
              top: '0',
              left: '0',
              bottom: '0',
              right: '0',
            }}
          />
        </div>
        <h3>Konfirmasi</h3>
        <div className="table-transaksi">
          <table className="table-transaksi">
            <thead>
              <tr>
                <th>Product</th>
                <th>Price</th>
                <th>QTY</th>
                <th>Total</th>
              </tr>
            </thead>
            <tbody>
              {data?.map((dat, ind) => (
                <tr key={ind}>
                  <td>{dat?.product}</td>
                  <td>{dat?.harga}</td>
                  <td>{dat?.jumlah}</td>
                  <td>{dat?.harga * dat?.jumlah}</td>
                </tr>
              ))}
            </tbody>
            <tfoot>
              <tr>
                <td colSpan="4" className="text-end">Rp. {numberWithCommas(total)}</td>
              </tr>
            </tfoot>
          </table>
        </div>
        <div className="d-flex gap-3" style={{ bottom: 20, right: 20 }}>
          <button className="submit-tr background-red300 p-2" onClick={() => setConfirm(false)}>
            Cancel
          </button>
          <button className="submit-tr p-2" onClick={submitT}>
            Confirm
          </button>
        </div>
      </div>

    </div>
  )
}

export default ModalTr
