import React, { useEffect, useState } from 'react'

const TransactionData = ({ data, setDta, setTO, setOP, OP }) => {

  const [p, setP] = useState(0);

  const dataSet = (index) => {
    data.splice(index, 1);
    OP.splice(index, 1);
    setDta([...data])
    setOP([...OP])
  }

  useEffect(() => {
    setP(0)
    for (let index = 0; index < data.length; index++) {
      setP(as => as  + data[index]?.harga * parseInt(data[index]?.jumlah))
    }
  }, [data]);

  useEffect(() => {
    setTO(p)
  }, [setTO, p]);

  return (
    <tbody>
      {data?.map((dat, index) => {
        return (
          <tr key={index}>
            <td>{dat?.product}</td>
            <td>{dat?.harga}</td>
            <td>{dat?.jumlah}</td>
            <td>{dat?.harga * parseInt(dat?.jumlah)}</td>
            <td><i onClick={e => {dataSet(index)}} className="material-icons color-red circle cursor-pointer">cancel</i></td>
          </tr>
        )
      })}
    </tbody>
  )
}

export default TransactionData
