import axios from 'axios';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react'
import UseAnimations from 'react-useanimations';
import menu4 from 'react-useanimations/lib/menu4';
import UseAuth from '../auth';
import Modal2 from '../body/Modal2';
import userPlus from 'react-useanimations/lib/userPlus'
import ListBar from './listBar';
import ModalC from '../body/modalC';

const Logout = async token => {
  return axios.post(
    'https://coperationv2.herokuapp.com/api/logout', {},
    {
      headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
      },
    })
   .then(data => {
     return data.data.message;
   })
   .catch(data => {
     return {'error': data}
   });
}

const Navbar = ({ setRed, user, sized }) => {

  const NItR = useRef();

  const [tooken, setTooken] = useState();
  const { setToken } = UseAuth();
  const [use, setUse] = useState(false);
  const [onModal, setModal] = useState(false);
  const [size, setSize] = useState(80);
  const [sizeA, setSizeA] = useState(24);
  const [visible, setVisible] = useState(true);
  const [list, setList] = useState(false);
  const [menuB, setMenuB] = useState(false);
  const [height, setHeight] = useState('auto');

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    setTooken(userToken?.token);
  }

  useEffect(() => {
    getToken();
  }, []);

  const handleSubmit = async e => {
    e.preventDefault();
    const tokenA = await Logout(tooken);
    if (tokenA?.error === undefined) {
      setToken({'logout': true});
      setRed(true)
    }
  }

  const addUser = (e) => {
    e.preventDefault();
    setUse(true);
  }

  const resizeW = () => {
    if (window.innerWidth <= 768) {
      setSize(60);
      setSizeA(15);
      setVisible(false);
    } else {
      setSize(80);
      setSizeA(24);
      setVisible(true);
    }
  }

  useEffect(() => {
    resizeW();
    window.addEventListener('resize', resizeW);
    return () => window.removeEventListener('resize', resizeW);
  }, [window.innerWidth]);

  const resizeH = () => {
    setHeight(`calc(${window.innerHeight}px - 60px)`)
  }

  useEffect(() => {
    resizeH();

    window.addEventListener('resize', resizeH);
    return () => window.removeEventListener('resize', resizeH);
  }, [window.innerHeight]);

  useEffect(() => {
    if (sized !== undefined) {
      setList(true)
    }
  }, []);

  const setB = () => {
    setMenuB(!menuB);
  }

  useEffect(() => {
    if (menuB) {
      NItR.current.classList.add('animate5')
    } else {
      NItR.current.classList.remove('animate5')
    }
  }, [menuB]);

  const myFunction = (e) => {
    var popup = document.getElementById('popNav');
    popup.classList.toggle("show");
  }

  return (
    <>
    <Modal2 use={use} setUse={setUse} />
    <ModalC use={onModal} setModal={setModal} />
    <div className="d-flex position-absolute justify-content-space-between w-100 nav-responsive" style={{ zIndex: 2 }}>
      <div className={"d-flex align-items-center position-relative " + (list ? 'topnav-responsive' : '' )} style={{ height: `${size}px`, padding: '20px' }}>
        <UseAnimations animation={menu4} strokeColor="white" className="icons" reverse={menuB} onClick={setB} />
        <div className={"gap-2" + (list ? ' nav-item-responsive' : '')} style={{ overflow: 'hidden auto', maxHeight: height }} ref={NItR}>
          <div className="navbar-brander align-items-center">
            <img src={process.env.PUBLIC_URL + '/image/icon.png'} alt="" height={`${size}px`} />
            <h2 className="nav-brand color-md-white">Kaufen Market</h2>
          </div>
          {list ? <ListBar /> : null}
        </div>
      </div>
      <div className="top-nav" style={{ height: `${size}px`, right: 0, zIndex: 1 }}>
        <ul className="top-item">
          {user === 'admin' && <li className="toa cursor-pointer" onClick={addUser}>
            <UseAnimations animation={userPlus} size={sizeA} strokeColor="white" />
          </li>}
          <li className="toa cursor-pointer">
            <i className="material-icons" onClick={(e) => {e.preventDefault(); setModal(true)}}>lock_open</i>
          </li>
          <li className="toa cursor-pointer position-relative">
            <i className="material-icons" onClick={myFunction}>notifications</i>
            <span className="popuptext popnav d-flex flex-direction-column whiteSpace-noWrap" id="popNav">
              <span>No Notifications !</span>
            </span>
          </li>
          <li className="toa d-flex align-items-center cursor-pointer" onClick={handleSubmit}>
            <i className="material-icons" style={{ marginRight: '5px' }}>exit_to_app</i>
            {visible ? <h4>Logout</h4> : null}
          </li>
        </ul>
      </div>
    </div>
    </>
  )
}

Navbar.propTypes = {
  setRed: PropTypes.func.isRequired
};

export default Navbar;
