import React, { useEffect, useState } from 'react';
import { 
  Switch,
  Route,
  BrowserRouter as Router
 } from "react-router-dom";
 import axios from 'axios';
import Login, { Forgot } from "./body/login";
import Home from './body/home';
import Template from './template';
import UseToken from './auth'
import AuthRoute from './auth/authroute';
import TemplateTR from './template/templateTr';
import LoaderSP from './body/loader';
import AuthRouter from './auth/authRouter';

function App() {

  const { token, setToken } = UseToken();
  const [role, setRole] = useState();

  let template = {
    admin: <Template role={role} setRole={setRole} />,
    penjual: <TemplateTR role={role} />,
    anggota: <TemplateTR role={role} />
  }

  useEffect(() => {
    if (token) {
      try {
        axios.get(
          'https://coperationv2.herokuapp.com/api/user/detail', 
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token
            }
          }
        ).then(data => {
          setRole(data.data.success);
        }).catch(() => {
          setRole(undefined);
        });
      } catch (error) {
        
      }
    }
  }, [token]);

  // if(!token && his[his.length-1] !== 'home' && his[his.length-1] !== 'forgot') {
  //   return <Login setToken={setToken} setRole={setRole} />
  // }

  return (
    <>
    <Router>
      <Switch>
        {/* <AuthRouter> */}
          <Route path="/login">
            <Login setToken={setToken} setRole={setRole} />
          </Route>
          <Route path="/forgot">
            <Forgot />
          </Route>
          <Route path="/home">
            <Home />
          </Route>
        {/* </AuthRouter> */}
        <AuthRoute path="/">
          {role !== undefined ? template[role?.role[0]] : <LoaderSP /> }
        </AuthRoute>
      </Switch>
    </Router>
    </>
  );
}

export default App;