import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router';
import BankData from '../data/bankData';
import Modal from './Modal';
import Popup from './Popup';

const Bank = ({ role }) => {

  const [search, setS] = useState();
  const [get, setGet] = useState({keyword: ''});
  const [product, addProduct] = useState(false);
  const [del, setdel] = useState([]);

  const history = useHistory();

  let require = {
    user_id: '',
    total: 0,
  }

  const searchH = e => {
    return setTimeout(() => {
      setGet({
        keyword: e.value
      })
    }, 500);
  }

  const serG = e => {
    e.preventDefault();
    clearInterval(search)
    setS(undefined);
    setS(searchH({
      value: e.target.value
    }));
  }

  useEffect(() => {
    if (role?.data?.[0] === undefined) {
      return history.push('/profile');
    }
  }, [role]);

  return (
    <div className="wrapi d-flex flex-direction-column w-100">
      <Popup del={del} keyword={setGet} setdel={setdel} href="nabung" />
      <Modal addProduct={addProduct} product={product} keyword={setGet} href="nabung" require={require} name="Saving" />
      <div className="wrap-he d-flex align-items-center justify-content-space-between">
        <div className="wrap-ty d-flex align-items-center gap-2">
          <h3>Savings</h3>
        </div>
        <div className="chart-se">
          <div className="role-se d-flex align-items-center">
            <i className="material-icons">search</i>
            <input type="text" onChange={serG} />
          </div>
        </div>
      </div>
      <div className="wrap-b">
        <table>
          <thead>
            <tr>
              <th>No</th>
              <th>Profile</th>
              <th>Name</th>
              <th>Total</th>
              <th>Date</th>
            </tr>
          </thead>
          <BankData keyword={get} addProduct={addProduct} setdel={setdel} />
        </table>
      </div>
    </div>
  )
}

export default Bank
