import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import Loader from 'react-loader-spinner';

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const Modal2 = ({ use, setUse }) => {

  const modalR = useRef(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [c_password, setCPassword] = useState('');
  const [role, setRole] = useState('');
  const [load, setLoad] = useState(false);

  const SPass = (e) => {
    e.preventDefault();
    if (e.target.value !== password) {
      e.target.classList.add('border-red');
      return false;
    }
    e.target.classList.remove('border-red');
    setCPassword(e.target.value)
  }

  useEffect(() => {
    if (use) {
      modalR.current.style.display = 'flex'
    } else {
      modalR.current.style.display = null
    }
  }, [modalR, use]);

  const handleSubmit = async e => {
    e.preventDefault();
    setLoad(true);
    const token = getToken();
    const datas = new FormData(e.target);
    const yu = await axios.post(
      `https://coperationv2.herokuapp.com/api/register`,
      datas,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      return {
        'success': data
      }
    }).catch(data => {
      return {
        'error': data
      }
    })

    if (yu?.error === undefined && yu !== undefined) {
      setLoad(false);
      setUse(false)
    } else {
      setLoad(false)
    }
  }

  useEffect(() => {
    if (load) {
      modalR.current.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      modalR.current.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  const modalClose = (e) => {
    if (e.target.children[0] === modalR.current.children[0]) {
      setUse(false)
    }
  }

  function readURL(input) {
    if (input.target.files && input.target.files[0]) {
      var reader = new FileReader();
      
      reader.onload = function(e) {
        document.getElementById('imgP').setAttribute('src', e.target.result);
      }
      
      reader.readAsDataURL(input.target.files[0]);
    }
  }

  return (
    <div id="modalRegister" className="modal justify-content-center align-items-start" style={{ padding: '100px 0' }} ref={modalR} onClick={modalClose} >

      <div className="modal-content col-5 col-md-7 col-sm-10 wrap-b position-relative d-flex flex-direction-column p-5 overflow-unset">
        <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
          <h3>Register</h3>
        </div>
        <div className="card-modal" style={{ zIndex: 1, left: '25%', right: '25%' }}>
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
            className="justify-content-center align-items-center d-flex"
            style={{
              position: 'absolute',
              top: '0',
              left: '0',
              bottom: '0',
              right: '0',
            }}
          />
        </div>
        <form action="#" onSubmit={handleSubmit} style={{height: '100%'}}>
          <div className="modal-body gap-2 d-flex align-items-center justify-content-center flex-direction-column p-3">
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="image">Image</label>
              <input className="w-100 input-modal background-gray600" onChange={readURL} type="file" accept=".png, .jpg, .jpeg" name="image" />
              <img width="100%" src="" alt="" id="imgP"/>
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="name">Name</label>
              <input className="w-100 input-modal background-gray600" type="text" name="name" onInput={e => setName(e.target.value)} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="email">Email</label>
              <input className="w-100 input-modal background-gray600" type="email" name="email" onInput={e => setEmail(e.target.value)} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="password">Password</label>
              <input className="w-100 input-modal background-gray600" type="password" name="password" onInput={e => setPassword(e.target.value)} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="c_password">Re password</label>
              <input className="w-100 input-modal background-gray600" type="password" name="c_password" onInput={SPass} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="role">Role</label>
              <div className="input-modal radio_select background-gray600">
                <div>
                  <input id="rad1" type="radio" name="role" defaultChecked value="admin" onInput={e => setRole(e.target.value)} />
                  <label htmlFor="rad1">admin</label>
                </div>

                <div>
                  <input id="rad2" type="radio" name="role" value="penjual" onInput={e => setRole(e.target.value)} />
                  <label htmlFor="rad2">penjual</label>
                </div>

                <div>
                  <input id="rad3" type="radio" name="role" value=  "anggota" onInput={e => setRole(e.target.value)} />
                  <label htmlFor="rad3">anggota</label>
                </div>
              </div>
            </div>
          </div>
          <div className="d-grid gap-2" style={{ gridTemplateColumns: '1fr 1fr' }}>
            <button className="submitF cursor-pointer background-red align-self-center" type="reset" onClick={() => setUse(false)}>
              <h4>Cancel</h4>
            </button>
            <button className="submitF cursor-pointer background-blues300 align-self-center">
              <h4>Create</h4>
            </button>
          </div>
        </form>
      </div>

    </div>
  )
}

export default Modal2
