import React, { useEffect, useRef, useState } from 'react'
import Loader from 'react-loader-spinner';
import TransactionData from '../data/transactionData';
import Transaksi from '../getAPI/transaksiGet';
import TransaksiPut from '../getAPI/transaksiPut';
import { SimpleSelect } from 'react-selectize';
import 'react-selectize/themes/index.css'
import ModalTr from './modalTr';

const Transaction = ({role}) => {

  const [data, setDta] = useState([]);
  const [OP, setOP] = useState([]);
  const [total, setTO] = useState(0);
  const [stock, setStock] = useState({});
  const [jumlah, setTot] = useState(0);
  const [harga, setHarga] = useState(0);
  const [load, setLoad] = useState(false);
  const [confirm, setConfirm] = useState(false);
  const [refresh, setRefresh] = useState([]);
  const [keyword, setKeyword] = useState('');

  const TRas = useRef();

  var today = new Date();
  var dd = String(today.getDate()).padStart(2, '0');
  var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = today.getFullYear().toString().substr(2);

  useEffect(() => {
    const getTransaksi = async () => {
      setStock(await Transaksi({
        keyword: keyword
      }))
    }
    getTransaksi();
  }, [refresh, keyword]);

  const AddTr = e => {
    e.preventDefault();

    let dy = {};

    for (let index = 0; index < e.target.length - 1; index++) {
      dy[e.target[index].name] = e.target[index].value
      if (e.target[index].name === 'product') {
        setOP([...OP, e.target[index][e.target[index].selectedIndex].id]);
      }
      // (dy)
    }

    dy.jumlah_asli = jumlah;

    for (let index = 0; index < data.length; index++) {
      if (data[index]?.product === document.getElementsByTagName('select')[0].value) {
        data[index].jumlah = document.getElementsByTagName('input').jumlah.value;
        setDta([...data])
        return true;
      }
    }

    setDta([...data, dy])
  }

  const totalSet = (e) => {
    // e.preventDefault();
    // console.log(e)
    let selected = e.target.selectedIndex;
    setTot(parseInt(e.target[selected].dataset.stock));
    setHarga(e.target[selected].dataset.harga)
  }

  const submitH = async (e) => {
    e.preventDefault();
    setLoad(true);
    const wi = await TransaksiPut({ id: OP, data: data });
    if (wi) {
      setLoad(false);
      setDta([]);
      setOP([]);
      setTO(0);
      setConfirm(false);
      setRefresh([...refresh])
    }
  }

  const resizeasd = () => {
    if (window.innerWidth <= 768) {
      TRas.current.classList.remove('gap-5')
      TRas.current.classList.add('gap-1')
    } else {
      TRas.current.classList.add('gap-5')
      TRas.current.classList.remove('gap-1')
    }
  }

  useEffect(() => {
    resizeasd();
    window.addEventListener('resize', resizeasd);
    return () => window.removeEventListener('resize', resizeasd);
  }, [window.innerWidth]);

  return (
    <div className="d-flex w-100 wrapi flex-flow-wrap h-100 h-md-auto" style={{ padding: 0 }}>
      <ModalTr confirm={confirm} setConfirm={setConfirm} submitT={submitH} load={load} data={data} total={total} />
      <div className="col-8 col-md-12 d-grid tr-tr h-100 h-md-auto" style={{ gridTemplateRows: '1fr 1fr' }}>
        <form className="h-100 wrap-he flex-direction-column d-flex justify-content-space-between" onSubmit={AddTr}>
          <div className="d-flex h-100 gap-5" ref={TRas}>
            <div className="d-flex flex-direction-column w-100 justify-content-space-between gap-1">
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="tanggal">
                    <p>Tanggal</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <input type="text" name="tanggal" disabled className="w-100 input-tr" value={ dd + ' / ' + mm + ' / ' + yyyy } />
                </div>
              </div>
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="penjual">
                    <p>Penjual</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <input type="text" name="penjual" disabled className="w-100 input-tr" value={ role?.name } />
                </div>
              </div>
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="harga">
                    <p>Harga</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <input type="text" name="harga" disabled className="w-100 input-tr" value={harga} />
                </div>
              </div>
            </div>
            <div className="d-flex flex-direction-column w-100 justify-content-space-between gap-1">
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="product">
                    <p>Product</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <select name="product" className="input-tr w-100 background-white" onInput={totalSet}>
                    <option hidden>Select Product</option>
                    {Object?.keys(stock)?.map(yu => {
                      return stock?.[yu]?.map((dat, index) => (
                        <option id={`${yu}-${dat?.id}`} key={index} value={dat?.name} data-harga={dat?.harga} data-stock={dat?.stock}>{dat?.name}</option>
                      ))
                    })}
                  </select>
                  {/* <SimpleSelect placeholder="Select a Product" options={Object.keys(stock).map(
                    yu => {
                      return stock?.[yu]?.map((dat, index) => ({label: dat?.name, value: dat?.name}))
                    }
                  )} onValueChange={totalSet} onSearchChange={(e) => setKeyword(e)}>
                    {(Object.keys(stock).length < 1) ? null : Object?.keys(stock)?.map(yu => {
                      return stock?.[yu]?.map((dat, index) => (
                        // <option id={`${yu}-${dat?.id}`} key={index} value={dat?.name} data-harga={dat?.harga} data-stock={dat?.stock}>{dat?.name}</option>
                        <option value={dat?.name}>{dat?.name}</option>
                      ))
                    })}
                  </SimpleSelect> */}
                </div>
              </div>
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="jumlah">
                    <p>Jumlah</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <input type="number" name="jumlah" min="1" max={jumlah} className="w-100 input-tr" />
                </div>
              </div>
              <div className="d-flex align-items-center flex-flow-wrap">
                <div className="col-6 col-md-12">
                  <label htmlFor="alamat">
                    <p>Alamat</p>
                  </label>
                </div>
                <div className="col-6 col-md-12">
                  <input type="text" name="alamat" disabled className="w-100 input-tr" value="Jalan Ini" />
                </div>
              </div>
            </div>
          </div>
          <div className="d-flex justify-content-end">
            <button className="submit-tr">Add</button>
          </div>          
        </form>
        <div className="h-100 wrap-b">
          <table>
            <thead>
              <tr>
                <th>Product</th>
                <th>Price</th>
                <th>QTY</th>
                <th>Total</th>
                <th></th>
              </tr>
            </thead>
            <TransactionData data={data} OP={OP} setOP={setOP} setDta={setDta} setTO={setTO} />
          </table>
        </div>
      </div>
      <div className="col tr-sm wrap-b">
        <div className="card-modal align-items-center justify-content-center">
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
          />
        </div>
        <div className="d-flex flex-direction-column">
          <div className="head-tr p-4">
            <h2>
              Detail
              Pemesanan
            </h2>
          </div>
          <div className="body-tr d-flex flex-direction-column">
            <div className="d-flex p-2">
              <div className="col d-flex align-items-center">
                <i className="material-icons pr-1">person_outline</i>
                Penjual
              </div>
              <div className="text-mute">
                {role?.name}
              </div>
            </div>
            <div className="d-flex p-2">
              <div className="col d-flex align-items-center">
                <i className="material-icons-outlined pr-1">location_on</i>
                Alamat
              </div>
              <div className="text-mute">
                Jalan Ini
              </div>
            </div>
            <div className="d-flex p-2">
              <div className="col d-flex align-items-center">
                <i className="material-icons pr-1">date_range</i>
                Date
              </div>
              <div className="text-mute">
                { dd + ' / ' + mm + ' / ' + yyyy }
              </div>
            </div>
            <hr/>
            <div className="d-flex p-2">
              <div className="col d-flex">
                <i className="material-icons-outlined pr-1">shopping_bag</i>
                Products
              </div>
              <div className="d-flex flex-direction-column">
                {data?.map((data, index) => (
                  <div className="text-mute d-flex justify-content-space-between" key={index}>
                    {data?.product} :<span className="pl-1"> {data?.jumlah}x</span>
                  </div>
                ))}
              </div>
            </div>
            <div className="d-flex p-2 pt-2">
              <div className="col d-flex align-items-center">
                <i className="material-icons-outlined pr-1">price_change</i>
                Total
              </div>
              <div className="color-red">
                Rp. {total}
              </div>
            </div>
            <div className="d-flex p-2 pt-2 justify-content-center">
              <button className="submit-tr p-2 w-75" onClick={() => setConfirm(true)}>Konfirmasi</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Transaction
