import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import Loader from 'react-loader-spinner';
import { Link, useHistory } from 'react-router-dom';

const LogCred = async cred => {
  return axios.post(
    'https://coperationv2.herokuapp.com/api/login', 
    {
      email: cred.email,
      password: cred.password
    },
    {
      headers: {
          'Content-Type': 'application/json'
      },
    })
   .then(data => data.data.success)
   .catch(data => {
     return {'error': data}
   });
}

export const Login = ({ setToken, setRole  }) => {

  const history = useHistory();

  const container = useRef();

  const [email, setEmail] = useState('');
  const [errorEmail, setErrorEmail] = useState(false);
  const [errorPassword, setErrorPassword] = useState(false);
  const [password, setPassword] = useState('');
  const [load, setLoad] = useState(false);
  const [landscape, setLandscape] = useState('h-100v align-items-center');

  const handleSubmit = async e => {
    e.preventDefault();
    setLoad(true);
    setRole(undefined)
    const token = await LogCred({
      email,
      password
    });
    setLoad(false);
    if (token?.error) {
      setPassword('');
      setEmail('');
      e.target[0].classList.add('border-error')
      e.target[1].classList.add('border-error')
      setErrorEmail(true);
      setErrorPassword(true);
    } else {
      setToken(token)
      return history.replace('/')
    }
  }

  const setFun = (e, tga, setError) => {
    tga(e.target.value);
    
    if (e.target.value === '') {
      e.target.classList.add('border-error')
      setError(true)
    } else {
      e.target.classList.remove('border-error')
      setError(false)
    }
  }

  useEffect(() => {
    if (load) {
      document.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      document.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  useEffect(() => {
    const submitB = document.getElementById('submit')
    if (email === '' || password === '') {
      submitB?.classList.add('button-mute')
      submitB.type = 'button';
    } else {
      submitB?.classList.remove('button-mute')
      submitB.type = 'submit';
    }
  }, [email, password]);

  const orientationC = () => {
    if (window.orientation === 90 && window.innerHeight <= 600) {
      setLandscape('pt-5 pb-5');
    } else {
      setLandscape('h-100v align-items-center')
    }
  }

  useEffect(() => {
    orientationC();
    window.addEventListener('orientationchange', orientationC);
    return () => window.removeEventListener('orientationchange', orientationC);
  }, []);

  return (
    // <BrowserRouter>
    <div className="container" ref={container}>
      <div className="row">
        <div className="col-6 col-md-12 d-flex d-md-none flex-direction-column justify-content-space-between h-100v" style={{ padding: '30px', paddingBottom: 0, background: 'linear-gradient(315deg, rgba(0,90,133,0.6) 30%, rgba(140,215,250,1) 80%)' }}>
          <h1 className="h1-log">Welcome, Users!</h1>
          <div className="image-log">
            <img src={process.env.PUBLIC_URL + '/image/Hands - Give.png'} style={{ transform: 'rotate(180deg)' }} width="45%" alt=""/>
            <img src={process.env.PUBLIC_URL + '/image/Hands - Phone.png'} style={{ transform: 'scaleX(-1)', filter: 'FlipH' }} width="45%" alt=""/>
          </div>
        </div>
        <div className={`col-6 col-md-12 d-flex justify-content-center ${landscape}`}>
          <div className="card-modal">
            <Loader
              type="Puff"
              color="rgba(0,90,133,1)"
              height={100}
              width={100}
              className="justify-content-center align-items-center d-flex"
              style={{
                position: 'absolute',
                top: '0',
                left: '0',
                bottom: '0',
                right: '0',
              }}
            />
          </div>
          <div className="card-log">
            <h1 style={{ color: 'black' }}>Login</h1>
            <p>Welcome back please login to your account</p>
            <form className="form-log d-flex flex-direction-column" onSubmit={handleSubmit}>
              <div className="form-row d-flex flex-direction-column">
                <div className="form-col label-log">
                  <label htmlFor="email">
                    <h4>Email</h4>
                  </label>
                </div>
                <div className="form-col input-log">
                  <input type="email" name="email" className="form-input" onInput={e => setFun(e, setEmail, setErrorEmail)} value={email} />
                </div>
                {errorEmail
                 ? <small style={{ color: 'red', fontStyle: 'italic' }}>Error Email</small>
                 : null
                }
              </div>
              <div className="form-row d-flex flex-direction-column">
                <div className="form-col label-log">
                  <label htmlFor="password">
                    <h4>Password</h4>
                  </label>
                </div>
                <div className="form-col input-log">
                  <input type="password" name="password" className="form-input" onInput={e => setFun(e, setPassword, setErrorPassword)} value={password} />
                </div>
                {errorPassword
                 ? <small style={{ color: 'red', fontStyle: 'italic' }}>Error Password</small>
                 : null
                }
              </div>
              <Link style={{ color: 'inherit' }} className="align-self-end" to="/forgot">
                Forgot Password ?
              </Link>
              <button type="submit" className="save-form save-log" id="submit">
                <h4>Login</h4>
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
    // </BrowserRouter>
  )
}

Login.propTypes = {
  setToken: PropTypes.func.isRequired
};

export const Forgot = () => {

  const [load, setLoad] = useState(false);
  const [size, setSize] = useState(29)
  const [landscape, setLandscape] = useState('h-100v align-items-center');

  const history = useHistory();

  const submitHandle = (e) => {
    e.preventDefault();

    axios.post(
      `https://coperationv2.herokupp.com/api/password/email`,
      {
        email: e.target[0].value
      }
    ).then(() => setLoad(true)).catch(data => console.log(data));
  }

  const backH = () => {
    history.goBack();
  }

  const onResize = () => {
    if (window.innerWidth <= 600) {
      setSize(20)
    } else {
      setSize(29)
    }
  }

  useEffect(() => {
    onResize();
    window.addEventListener('resize', onResize);

    return () => window.removeEventListener('resize', onResize)
  }, []);

  const orientationC = () => {
    if (window.orientation === 90 && window.innerHeight <= 600) {
      setLandscape('pt-5 pb-5');
    } else {
      setLandscape('h-100v align-items-center')
    }
  }

  useEffect(() => {
    orientationC();
    window.addEventListener('orientationchange', orientationC);
    return () => window.removeEventListener('orientationchange', orientationC);
  }, []);

  return (
    <div className="container">
      <div className={`row justify-content-center ${landscape}`}>
        <div className="col-5 col-md-7">
          <div className="wrap-he">
            <div className="circleHa cursor-pointer d-flex align-items-center justify-content-center" onClick={backH} style={{ width: size, height: size, background: 'rgba(230, 230, 230, 1)' }}>
              <i className="material-icons size-20">arrow_back</i>
            </div>
            <h3 className="text-center">Forgot Password</h3>
            {load ?
             <p>Email Telah dikirim</p>
             : <form className="d-flex flex-direction-column pt-1 gap-2" onSubmit={submitHandle}>
             <div className="form-row d-flex flex-direction-column">
               <div className="form-col input-log">
                 <input type="email" name="email" className="form-input" placeholder="Email" />
               </div>
             </div>
             <button type="submit" className="save-form save-log" id="submit">
               <h4>Send Email</h4>
             </button>
           </form>
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login;
