import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import Loader from 'react-loader-spinner';

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const Modal = ({ addProduct, product, keyword, href, require, name }) => {

  const modal = useRef(null);
  const buttonR = useRef(null);
  const [load, setLoad] = useState(false);
  const [user, setUser] = useState([]);

  useEffect(() => {
    if (product) {
      modal.current.style.display = 'flex'
    } else {
      modal.current.style.display = null
    }
  }, [product, modal]);

  const handleSubmit = async e => {
    e.preventDefault();
    setLoad(true);
    const token = getToken();
    const datas = new FormData(e.target);
    if (datas?.get('user_id') !== null && datas?.get('user_id') !== undefined) {
      datas.append('data_id', datas?.get('user_id'));
    }
    const asd = await axios.post(
      `https://coperationv2.herokuapp.com/api/${href}`,
      datas,
      {
        headers: {
          'content-type': 'multipart/form-data',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      return {
        'success': data
      }
    }).catch(data => {
      return {
        'error': data
      }
    })
    if (asd?.error === undefined && asd !== undefined) {
      keyword(asd => {return {...asd}})
      setLoad(false)
      buttonR.current.click();
    } else {
      setLoad(false);
    }
  }

  useEffect(() => {
    if (load) {
      modal.current.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      modal.current.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  const modalClose = (e) => {
    if (e.target.children[0] === modal.current.children[0]) {
      addProduct(false)
    }
  }

  function readURL(input) {
    if (input.target.files && input.target.files[0]) {
      var reader = new FileReader();
      
      reader.onload = function(e) {
        document.getElementById('imgP').setAttribute('src', e.target.result);
        require.image = input.target.files[0]
      }
      
      reader.readAsDataURL(input.target.files[0]);
    }
  }

  useEffect(() => {
    axios.get(
      `https://coperationv2.herokuapp.com/api/users`,
      {
        headers: {
          Authorization : `Bearer ${getToken()}`,
        }
      }
    ).then(data => setUser(data.data.success))
  }, []);

  const UserOpt = () => {
    let opt = [];
    for (let index = 0; index < user?.length; index++) {
      opt.push(<option key={index} value={user[index]?.id}>{user[index]?.name}</option>);
    }
    return opt;
  }

  return (
    <div id="modalProduct" className="modal justify-content-center align-items-start" style={{ padding: '100px 0' }} ref={modal} onClick={modalClose}>

      <div className="modal-content col-5 col-md-7 col-sm-10 wrap-b position-relative d-flex flex-direction-column p-5 overflow-unset">
        <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
          <h3>Add {name}</h3>
        </div>
        <div className="card-modal" style={{ zIndex: 1, left: '25%', right: '25%' }}>
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
            className="justify-content-center align-items-center d-flex"
            style={{
              position: 'absolute',
              top: '0',
              left: '0',
              bottom: '0',
              right: '0',
            }}
          />
        </div>
        <form action="#" onSubmit={handleSubmit} encType="multipart/form-data" className="form-wrap d-flex flex-direction-column">
          {Object.keys(require).map((data, index) => (
            <div className="form-row d-flex flex-direction-column" key={index}>
              <div className="form-col">
                <label htmlFor={data}>
                  <h4>{data.toUpperCase()}</h4>
                </label>
              </div>
              <div className="form-col">
                {/* {(data === "harga") ? <h4 className="pr-1">RP.</h4> : null} */}
                {(Object.keys(require)[index] === 'user_id')
                 ? <select name="user_id" className="w-100 input-modal background-gray600">
                   <option value="" hidden>Select User</option>
                   <UserOpt />
                 </select>
                 : <input name={Object.keys(require)[index]} type={(Object.keys(require)[index] === 'image') ? 'file' : (typeof require[data] === "object") ? 'date' : typeof require[data] } onChange={(Object.keys(require)[index] === 'image') ? readURL : null} accept=".jpg, .jpeg, .png" defaultValue={(Object.keys(require)[index] === 'image') ? null : require[data]} name={data} min="0" className="w-100 input-modal background-gray600" onInput={e => require[data] = ((isNaN(e.target.valueAsNumber) || e.target.valueAsDate !== null) ? (Object.keys(require)[index] === 'image') ? '' : e.target.value : e.target.valueAsNumber)} />
                }
                {(Object.keys(require)[index] === 'image') ? <img width="100%" id="imgP" src="" alt="" /> : null}
              </div>
            </div>
          ))}
          <div className="d-grid gap-2" style={{ gridTemplateColumns: '1fr 1fr' }}>
            <button ref={buttonR} className="submitF cursor-pointer background-red align-self-center" type="reset" onClick={() => addProduct(false)}>
              <h4>Cancel</h4>
            </button>
            <button className="submitF cursor-pointer background-blues300 align-self-center">
              <h4>Add</h4>
            </button>
          </div>
        </form>
      </div>

    </div>
  )
}

export default Modal
