import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';

const Home = () => {

  const [size, setSize] = useState(80);
  const [sized, setSized] = useState('100%');
  const homeC = useRef();

  const resized = () => {
    if (window.innerWidth <= 768) {
      setSize(60)
    } else {
      setSize(80);
    }
  }

  useEffect(() => {
    resized();
    window.addEventListener('resize', resized);
    return () => window.removeEventListener('resize', resized)
  }, []);

  const oriented = () => {
    if (window.orientation === 90 && window.innerHeight <= 600) {
      setSized('500px');
    } else {
      setSized('100%')
    }
  }

  useEffect(() => {
    oriented();
    window.addEventListener('orientationchange', oriented)
    return () => window.removeEventListener('orientationchange', oriented);
  }, []);

  return (
  <div className="container h-100">
    <div className="row h-100" style={{ backgroundColor: 'white' }}>
      <div className="col-12 d-flex justify-content-space-between" style={{ height: `${size}px`, padding: '0px 20px' }}>
        <div className="navbar-brander align-items-center">
          <img src={process.env.PUBLIC_URL + '/image/icon.png'} alt="" height={`${size}px`} />
          <h2 className="nav-brand">Kaufen Market</h2>
        </div>
        <div className="nav-right">
          <ul className="navbar-nav">
            <li className="nav-item">
              <span className="nav-link">
                <Link to="/login" className="lg-button">
                  Login
                </Link>
              </span>
            </li>
          </ul>
        </div>
      </div>
      <div className="col-12" style={{ height: `calc(${sized} - ${size}px)`, padding: '20px', paddingTop: 0 }}>
        <div className="home-content d-flex h-100 align-items-center" ref={homeC} style={{ borderRadius: '20px', background: 'linear-gradient(135deg, rgba(0,90,133,1) 30%, rgba(140,215,250,1) 70%)', padding: '20px', color: 'white' }}>
          <div className="col-6 col-md-12 align-items-center">
            <h1 className="h1-log">Koperasi Simpan Pinjam.</h1>
            <p className="w-50 size-20" style={{ marginTop: '10px' }}>koperasi sekaligus tempat simpan pinjam.</p>
            <img src={process.env.PUBLIC_URL + '/image/google-play-badge.png'} alt="" height="30" style={{ marginTop: '30px', cursor: 'pointer' }}  />
          </div>
          <div className="col-6 col-md-12 d-md-none">
            <div className="d-flex h-100 flex-direction-column">
              <div className="col-12 justify-content-center d-flex">
                <div className="circleHa" style={{ height: '280px', width: '280px' }}>
                  <div className="circleHa justify-content-center align-items-center d-flex" style={{ height: `${size}px`, width: `${size}px`, top: -20, zIndex: 2, left: 40, background: 'black' }}>
                    <h5 style={{ textAlign: 'center', width: '50%', transform: 'rotate(-30deg)' }}>100% Tanpa <span style={{ color: 'yellow' }}>Riba!</span></h5>
                  </div>
                </div>
                <img src={process.env.PUBLIC_URL + '/image/hnd.png'} alt="" style={{ width: '450px', zIndex: 2 }} />
              </div>
              <div className="col-12 justify-content-space-around d-flex" style={{ marginTop: '30px' }}>
                <div className="circleHa" style={{ width: '150px', height: '150px', left: 0 }}></div>
                <div className="circleHa" style={{ width: '100px', height: '100px', bottom: -30 }}></div>
                <img src={process.env.PUBLIC_URL + '/image/1.jpg'} alt="" style={{ height: '120px', zIndex: 2 }} />
                <img src={process.env.PUBLIC_URL + '/image/2.jpg'} alt="" style={{ height: '120px', zIndex: 2 }} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
};

export default Home;
