import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import moment from 'moment';
import { findDOMNode } from 'react-dom';
import UseAnimations from 'react-useanimations';
import checkbox from 'react-useanimations/lib/checkBox'
import LoaderData from './loaderdata';
import GetUserId from '../getAPI/getUserId';

const BankData = ({ keyword, addProduct, setdel }) => {

  const [itemDet, setItem] = useState({
    data: null
  });
  const [loading, setLoad] = useState(true);
  const [page, setPage] = useState(1);
  const [popi, setPop] = useState();
  const [test, setTest] = useState([]);

  const input = useRef();

  let inpo = document.createElement('input');

  useEffect(() => {
    var t = [];
    for (let index = 0; index < itemDet?.data?.length; index++) {
      t[index] = false
    }
    setTest(t)
  }, [itemDet]);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  useEffect(() => {
    const token = getToken();

    const getItem = async () => {
      const datt = await axios.get(
        'https://coperationv2.herokuapp.com/api/nabung?keyword=' + keyword?.keyword + '&page=' + page, 
        {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + token
          }
        }
      ).then(data => data?.data);

      for (let index = 0; index < datt?.data?.length; index++) {
        const user = await GetUserId({
          id: datt?.data?.[index]?.user_id,
          token: token,
        }).then(data => data?.data?.user?.[0]);
        datt.data[index].name = user?.name;
        datt.data[index].image = user?.image;
      }
      console.log(datt)
      setItem(datt);
      setLoad(false)
    }
    getItem();
  }, [keyword, page])

  const nextHandle = (e) => {
    setPage(parseInt(e.split('=')[1]))
  }

  const getAll = () => {
    return document.querySelectorAll('.tr-hover');
  }

  const clickHandle = e => {
    e.preventDefault();
    // (test)
    var r = parseInt(e.target.id);
    test[r] = !test[r];
    e.target.checked = test[r];
    if (e.target.checked) {
      e.target.parentElement.parentElement.parentElement.classList.add('tr-hover')
      e.target.parentElement.classList.remove('d-none')
    } else {
      e.target.parentElement.parentElement.parentElement.classList.remove('tr-hover')
      e.target.parentElement.classList.add('d-none')
    }
    // (test)
    setPop(getAll());
    // (popi)
  }

  useEffect(() => {
    if (popi?.length !== 0) {
      setdel(popi)
    } else {
      setdel(popi)
    }
  }, [popi, setdel]);

  const tnInput = (e) => {
    inpo.value = e.target.innerText;
    inpo.className = 'input-table';
    // eslint-disable-next-line
    if (parseInt(e.target.innerText) == e.target.innerText) {
      inpo.type = 'number'
    }
    e.target.childNodes[e.target.childNodes.length-1].data = "";
    e.target.appendChild(inpo);
    input.current = e.target;
  }

  useEffect(() => {
    var is = document.getElementsByTagName('tbody');
    if (!loading) {
      var ui = findDOMNode(is[0]).getElementsByTagName('td');

      for (let index = 0; index < ui.length; index++) {
        if (ui[index].dataset['not']) {
          continue;
        }
        ui[index].addEventListener('dblclick', tnInput)
      }
    }
    //eslint-disable-next-line
  }, [loading, itemDet]);

  useEffect(() => {
    handleClickOutside(input);
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
        // Unbind the event listener on clean up
        document.removeEventListener("mousedown", handleClickOutside);
    };
    // eslint-disable-next-line
  }, [input]);

  const handleClickOutside = async (event) => {
    const token = getToken();

    try {
      var io = input.current.parentElement;
      if (input && !input.current.contains(event.target) && input.current.children[0] !== "input") {
        input.current.childNodes[input.current.childNodes.length-2].data = input.current.lastElementChild.value;
        input.current.removeChild(input.current.childNodes[input.current.childNodes.length-1])
        var as = await axios.put(
          'https://coperationv2.herokuapp.com/api/nabung/' + parseInt(io.id),
          {
            name: io.children[2]?.innerText,
            total: io.children[4]?.innerText,
          },
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + token
            }
          }
        ).then(data => {
          return data
        }).catch(data => {
          return {
            error: data
          }
        });
        if (as?.error === undefined) {
        }
      }
    } catch (e) {
    }
  }

  if (!loading) {
    return (
      <>
      <tbody>
        {(itemDet?.data.length === 0)
         ? <tr>
           <td colSpan="6" data-not="true">
             <h3 className="color-red">No Data Found !</h3>
           </td>
         </tr>
         : itemDet.data.map((data, index) => {
          return <tr key={index} id={data?.id}>
             <td data-not="true">
               <div className="position-absolute check d-none">
                 <input type="checkbox" className='d-none' onClick={clickHandle} id={index+'check'} />
                 <label htmlFor={index+'check'}>
                   <UseAnimations animation={checkbox} reverse={test[index]} />
                 </label>
               </div>
               {index+1}
             </td>
             <td data-not="true">
              <div className="img-profile position-relative" style={{ width: '100%', height: 0, paddingTop: '100%' }}>
                <img src={(data?.image) ? `https://coperationv2.herokuapp.com/img_profile/${data?.image}` : process.env.PUBLIC_URL + '/user.jpg' } alt="" className="position-absolute" style={{ top: 0 }} />
              </div>
             </td>
             <td>{data?.name}</td>
             <td>{data?.total}</td>
             <td>{moment(data?.created_at).format('DD-MM-YYYY')}</td>
           </tr>
        })}
      </tbody>
      <tfoot>
        <tr>
          <td colSpan="7">
            <div className="d-flex justify-content-space-between align-items-center">
              <div className="back">
                <ul className="background-blues300 pagination d-flex flex-align-column align-items-center" style={{ borderRadius: '10px', overflow: 'hidden', }}>
                  {itemDet.links.map((data, index) => (
                    <li className={((data.active) ? 'active' : '') + ' pagination-item color-white' + ((data.url !== null) ? '' : ' button-mute' )} key={index} onClick={() => nextHandle(data.url)}>
                      {(data === itemDet.links[0])
                       ? <>&laquo;</>
                       : (data === itemDet.links[itemDet.links.length-1])
                        ? <>&raquo;</>
                        : data.label
                      }
                    </li>
                  ))}
                </ul>
              </div>
              <div className="front">
                <button className="color-white cursor-pointer" style={{ borderRadius: '10px', background: 'rgba(0,90,133,1)', padding: '10px 15px' }} onClick={() => addProduct(true)}>
                  + Add Saving
                </button>
              </div>
            </div>
          </td>
        </tr>
      </tfoot>
      </>
    )
  } else {
    return (
      <tbody>
        <tr>
          <td colSpan="6">
            <LoaderData />
          </td>
        </tr>
      </tbody>
    )
  }
}

export default BankData
