import React, { useState } from 'react';
import { 
  BrowserRouter as Router,
  Redirect,
  Route, 
  Switch
 } from 'react-router-dom';
import ChartFull from '../body/chartFull';
import Transaction from '../body/transaction';
import Navbar from "./navbar";

const TemplateTR = ({role}) => {

  const [red, setRed] = useState(false);

  if (red) {
    return <Redirect to="/home" />
  }

  let temRole = {
    penjual: <Transaction role={role} />,
    anggota: <ChartFull role={role} />
  }

  return (
    <Router basename="/">
      <div className="container">
        <div className="row">
          <Navbar setRed={setRed} />
          <div className="col d-flex h-100v flex-direction-column p-4" style={{ overflow: 'auto', paddingTop: 100 }}>
            <Switch>
              <Route exact path="/">
                {temRole[role.role[0]]}
              </Route>
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  )
}

export default TemplateTR;
