import axios from 'axios'

const Transaksi = async ({ keyword }) => {

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  const h = await axios.get(
    `https://coperationv2.herokuapp.com/api/barang-penjual/show?keyword=${keyword}`,
    {
      headers: {
        Authorization: `Bearer ${getToken()}`
      }
    }
  ).then(data => data.data.data).catch(data => data)

  const t = await axios.get(
    `https://coperationv2.herokuapp.com/api/barang-koperasi/show?keyword=${keyword}`,
    {
      headers: {
        Authorization: `Bearer ${getToken()}`
      }
    }
  ).then(data => data.data.data).catch(data => data)

  // console.log(h)
  for (let index = 0; index < h.length; index++) {
    h[index].name = h[index].name_barang + ' - ' + h[index].user_id
  }

  let arr = {
    koperasi: t,
    penjual: h
  }

  return arr;
}

export default Transaksi
