import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';

const postData = async ({ data, name, token, method }) => {
  return axios({
    method: method,
    url: `https://coperationv2.herokuapp.com/api/data${name === undefined ? '' : '/'+name}`,
    data: data,
    headers: {
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + token
    }
  })
  .then(data => data)
  .catch(data => {
    return {
      error: data
    }
  })
}

const Profile = ({ setRole }) => {

  const [userDet, setUser] = useState({});
  const [load, setLoad] = useState(true);
  const [get, setGet] = useState([]);
  
  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }
  
  const token = getToken();

  useEffect(() => {
    axios.get(
      'https://coperationv2.herokuapp.com/api/user/detail',
      {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      setRole(data.data.success);
      setUser(data.data.success);
      setLoad(false);
    });
  }, [get, token])

  const submitHandle = async (e) => {
    e.preventDefault();
    let arr = {};
    let method = 'PUT';

    for (let index = 0; index < e.target.length - 1; index++) {
      arr[e.target[index].name] = e.target[index].value
    }

    arr['user_id'] = userDet?.id;

    if (userDet?.data < 1) {
      method = 'POST';
    }

    const hi = await postData({
      data: arr,
      name: userDet?.data[0]?.name,
      token: token,
      method: method
    });

    if (hi?.error !== undefined) {
    }
    setGet([...get]);
  }

  if (!load) {
    return (
      <div className="profile-wrap d-flex flex-direction-column">
        <div className="profile-head d-grid gap-2" style={{ gridTemplateColumns: '3fr 1fr' }}>
          <div className="col">
            <h1>{userDet?.data[0]?.name}</h1>
            <p>{userDet?.email}</p>
            <p>Telp: {userDet?.data[0]?.no_telp}</p>
          </div>
          <div className="col">
            <div className="position-relative">
              <div className="img-profile position-relative" style={{ width: '100%', height: 0, paddingTop: '100%' }}>
                <img src={`https://coperationv2.herokuapp.com/img_profile/${userDet?.image}`} alt="" className="position-absolute" style={{ top: 0 }} />
              </div>
              <div className="circleHa d-flex align-items-center justify-content-center" style={{ width: '40px', height: '40px', background: '#8cd7fa', right: 0, bottom: 0, userSelect: 'none' }}>
                <Link to="/edit-user" className="material-icons" style={{ color: 'black' }}>mode</Link>
              </div>
            </div>
          </div>
        </div>
        <div className="profile-body position-relative d-flex flex-direction-column">
          <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
            <h3>My Profile</h3>
          </div>
          <form action="#" className="form-wrap d-flex flex-direction-column" onSubmit={submitHandle}>
            <div className="form-row d-flex flex-direction-column">
              <div className="form-col">
                <label htmlFor="name">
                  <h4>Name</h4>
                </label>
              </div>
              <div className="form-col">
                <input type="text" name="name" className="form-input" defaultValue={userDet?.data[0]?.name} />
              </div>
            </div>
            <div className="form-row d-flex flex-direction-column">
              <div className="form-col">
                <label htmlFor="no_telp">
                  <h4>No Telephone</h4>
                </label>
              </div>
              <div className="form-col">
                <input type="number" name="no_telp" className="form-input" defaultValue={userDet?.data[0]?.no_telp} />
              </div>
            </div>
            <div className="form-row d-flex flex-direction-column">
              <div className="form-col">
                <label htmlFor="tanggal_lahir">
                  <h4>Tanggal Lahir</h4>
                </label>
              </div>
              <div className="form-col">
                <input type="date" name="tanggal_lahir" className="form-input" defaultValue={moment(userDet?.data[0]?.tanggal_lahir).format('YYYY-MM-DD')} />
              </div>
            </div>
            <div className="form-row d-flex flex-direction-column">
              <div className="form-col">
                <label htmlFor="alamat">
                  <h4>Alamat</h4>
                </label>
              </div>
              <div className="form-col">
                <input type="text" name="alamat" className="form-input" defaultValue={userDet?.data[0]?.alamat} />
              </div>
            </div>
            <button className="save-form align-self-center">
              <h4>Save Changes</h4>
            </button>
          </form>
        </div>
      </div>
    )
  } else {
    return (
      <>
      </>
    )
  }
}

export default Profile
