import AutoSizer from "react-virtualized-auto-sizer";
import React, { useEffect, useState } from 'react'
import axios from "axios";
import GetUserId from "../getAPI/getUserId";

const ChartFull = ({ role }) => {
  
  const Bar = require('@nivo/bar');
  const Bardat = Bar.Bar;
  const Line = require('@nivo/line');
  const Linedat = Line.Line;
  const Pie = require('@nivo/pie');
  const Piedat = Pie.Pie;

  const [stock, setStock] = useState({
    koperasi: [],
    anggota: []
  });
  const [dataB, setdataB] = useState([]);
  const [stocks, setStocks] = useState([]);

  const [savings, setSavings] = useState([]);
  const [loans, setLoans] = useState([]);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  useEffect(() => {
    const getStock = async () => {
      const x = await axios.get(
        `https://coperationv2.herokuapp.com/api/barang-koperasi/show`,
        {
          headers: {
            Authorization: `Bearer ${getToken()}`
          }
        }
      ).then(data => data.data.data);

      const y = await axios.get(
        `https://coperationv2.herokuapp.com/api/barang-penjual/show`,
        {
          headers: {
            Authorization: `Bearer ${getToken()}`
          }
        }
      ).then(data => data.data.data);

      setStock({
        koperasi: x,
        anggota: y
      });
    }
    getStock();
  }, []);

  useEffect(() => {
    const getSavings = async () => {
      const s = await axios.get(
        `https://coperationv2.herokuapp.com/api/nabung/show`,
        {
          headers: {
            Authorization: `Bearer ${getToken()}`
          }
        }
      ).then(data => data.data.data);

      for (let index = 0; index < s.length; index++) {
        const user = await GetUserId({
          id: s?.[index]?.user_id,
          token: getToken(),
        }).then(data => data?.data?.user[0]?.name)
        s[index].name = user;
      }

      console.log(s)

      setSavings(s);
    }
    getSavings();
  }, []);

  useEffect(() => {
    const getLoans = async () => {
      const s = await axios.get(
        `https://coperationv2.herokuapp.com/api/pinjam/show`,
        {
          headers: {
            Authorization: `Bearer ${getToken()}`
          }
        }
      ).then(data => data.data.data);

      for (let index = 0; index < s.length; index++) {
        const user = await GetUserId({
          id: s?.[index]?.user_id,
          token: getToken(),
        }).then(data => data?.data?.user[0]?.name);
        s[index].name = user;
      }

      setLoans(s);
    }
    getLoans();
  }, []);

  const senin = {Pengeluaran: 30, Pendapatan: 40};
  const selasa = {Pengeluaran: 50, Pendapatan: 34};
  const rabu = {Pengeluaran: 40, Pendapatan: 27};
  const kamis = {Pengeluaran: 19, Pendapatan: 28};
  const jumat = {Pengeluaran: 23, Pendapatan: 10};
  const sabtu = {Pengeluaran: 37, Pendapatan: 16};
  const minggu = {Pengeluaran: 34, Pendapatan: 30};

  const CustomSymbol = ({ size, color, borderWidth, borderColor }) => (
    <g>
      <circle fill="#fff" r={size / 2} strokeWidth={borderWidth} stroke={borderColor} />
      <circle
        r={size / 5}
        strokeWidth={borderWidth}
        stroke={borderColor}
        fill={color}
        fillOpacity={0.35}
      />
    </g>
  )

  useEffect(() => {
    let yu = [];
    let ss = [];
    for (let index = 0; index < Object?.keys(stock)?.length; index++) {
      let io = Object.keys(stock)[index];
      yu.push({
        id: io,
      })
      stock?.[io]?.map(dat => {
        yu[index][(io === 'anggota') ? dat?.name_barang : dat?.name] = dat?.stock;
        ss.push((io === 'anggota') ? dat?.name_barang : dat?.name);
      })
      setStocks(ss);
    }
    setdataB(yu);
  }, [stock]);

  const Savings = () => {
    let tb = [];
    for (let index = 0; index < savings?.length; index++) {
      tb.push(
        <tr key={index}>
          <td>{savings?.[index]?.name}</td>
          <td>{savings?.[index]?.total}</td>
        </tr>
      );
    }
    return tb;
  }

  const Loans = () => {
    let tb = [];
    for (let index = 0; index < loans?.length; index++) {
      tb.push(
        <tr key={index}>
          <td>{loans?.[index]?.name}</td>
          <td>{loans?.[index]?.total}</td>
          <td>{loans?.[index]?.return}</td>
        </tr>
      );
    }
    return tb;
  }

  const dataL = [
    {
      id: 'Pengeluaran',
      colors: 'red',
      data: [
        {
          x: 'senin',
          y: senin.Pengeluaran
        },
        {
          x: 'selasa',
          y: selasa.Pengeluaran
        },
        {
          x: 'rabu',
          y: rabu.Pengeluaran
        },
        {
          x: 'kamis',
          y: kamis.Pengeluaran
        },
        {
          x: 'jumat',
          y: jumat.Pengeluaran
        },
        {
          x: 'sabtu',
          y: sabtu.Pengeluaran
        },
        {
          x: 'minggu',
          y: minggu.Pengeluaran
        }
      ]
    },
    {
      id: 'Pendapatan',
      colors: 'rgba(0,90,133,1)',
      data: [
        {
          x: 'senin',
          y: senin.Pendapatan
        },
        {
          x: 'selasa',
          y: selasa.Pendapatan
        },
        {
          x: 'rabu',
          y: rabu.Pendapatan
        },
        {
          x: 'kamis',
          y: kamis.Pendapatan
        },
        {
          x: 'jumat',
          y: jumat.Pendapatan
        },
        {
          x: 'sabtu',
          y: sabtu.Pendapatan
        },
        {
          x: 'minggu',
          y: minggu.Pendapatan
        }
      ]
    }
  ]

  const dataP = [
    {
      id: 'Senin',
      labels: 'Senin',
      value: 10
    },
    {
      id: 'Selasa',
      labels: 'Selasa',
      value: 24
    },
    {
      id: 'Rabu',
      labels: 'Rabu',
      value: 44
    },
    {
      id: 'Kamis',
      labels: 'Kamis',
      value: 17
    }
  ]

  return (
    <div className="wrapi w-100 d-flex justify-self-baseline flex-direction-column">
      <div className="h-50v d-grid gap-3 h-ld-100v">
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3>Laporan Stock</h3>
          <div className="chart h-100 pt-1">
            <AutoSizer style={{ width: '100%' }}>
              {({height, width}) => {
                return (
                  React.createElement(
                    Bardat,
                    {
                      height: height,
                      width: width,
                      data: dataB,
                      keys: stocks,
                      indexBy:"id",
                      margin: { top: 15, right: 15, left: 15, bottom: 35 },
                      padding: 0.3,
                      innerPadding: 5,
                      groupMode: "grouped",
                      valueScale: { type: 'linear' },
                      indexScale: { type: 'band', round: true },
                      colors: { scheme: 'nivo' },
                      axisTop: null,
                      axisRight: null,
                      axisBottom: null,
                      axisLeft: null,
                      labelSkipWidth: 12,
                      labelSkipHeight: 12,
                      labelTextColor: { from: 'color', modifiers: [ [ 'darker', 1.6 ] ] },
                      animate: true,
                      motionStiffness: 90,
                      motionDamping: 15,
                    }
                  )
                )
              }}
            </AutoSizer>
          </div>
        </div>
      </div>
      <div className="h-50v h-md-100v h-ld-200v d-grid gap-3 grid-template-rows" style={{ gridTemplateColumns: '8fr 4fr' }}>
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3>Laporan Keuangan</h3>
          <div className="chart h-100 pt-1">
          <AutoSizer style={{ width: '100%' }}>
              {({height, width}) => (
                React.createElement(
                  Linedat,
                  {
                    width: width,
                    height: height,
                    crosshairType: 'cross',
                    data: dataL,
                    curve: 'natural',
                    margin: { top: 15, right: 15, bottom: 35, left: 15 },
                    xScale: { type: 'point' },
                    yScale: { type: 'linear', min: '0', max: 'auto', reverse: false },
                    yFormat: " >-.2f",
                    colors: { datum: 'colors' },
                    axisTop: null,
                    axisRight: null,
                    axisBottom: null,
                    axisLeft: null,
                    pointSize: 10,
                    pointSymbol: CustomSymbol,
                    pointColor: { from: 'color' },
                    pointBorderWidth: 2,
                    pointBorderColor: { from: 'serieColor' },
                    useMesh: true,
                    enableSlices: 'x',
                    sliceTooltip: ({ slice }) => {
                      return (
                        <div className="wrap-b" style={{ padding: '10px 15px' }}>
                          {slice.points.map(point => (
                            <div
                              key={point.id}
                              style={{
                                  color: point.serieColor,
                                  padding: '3px 0',
                              }}
                            >
                              <strong>{`${point.serieId} hari ${point.data.xFormatted}`}</strong>: [{point.data.yFormatted}]
                            </div>
                          ))}
                        </div>
                      )
                    }
                  }
                )
              )}
            </AutoSizer>
          </div>
        </div>
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3>Laporan Product</h3>
          <div className="chart h-100 pt-1">
          <AutoSizer style={{ width: '100%' }}>
            {({height, width}) => (
              React.createElement(
                Piedat,
                {
                  width: width,
                  height: height,
                  data: dataP,
                  margin: { top: 15, right: 15, bottom: 35, left: 15 },
                  innerRadius: 0.5,
                  padAngle: 0.7,
                  cornerRadius: 3,
                  colors: { scheme: 'nivo' },
                  borderWidth: 1,
                  borderColor: { from: 'color', modifiers: [ [ 'darker', 0.2 ] ] },
                  enableRadialLabels: false,
                  sliceLabel: 'id',
                  sliceLabelsSkipAngle: 10,
                  sliceLabelsTextColor: "#333333"
                }
              )
            )}
          </AutoSizer>
          </div>
        </div>
      </div>
      <div className="h-50v h-ld-100v d-grid gap-3">
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3 className="pb-1">Laporan Transaksi</h3>
          <div className="table-wrap">
            <table>
              <thead>
                <tr>
                  <th>Hari</th>
                  <th>Product</th>
                  <th>Jenis</th>
                  <th>QTY</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Senin</td>
                  <td>Ini</td>
                  <td>makanan</td>
                  <td>12</td>
                  <td>Rp. 2000</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div className="h-50v h-md-100v h-ld-200v d-grid grid-template-rows gap-3">
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3 className="pb-1">Laporan Tabungan</h3>
          <div className="table-wrap">
            <table>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                <Savings />
              </tbody>
            </table>
          </div>
        </div>
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3 className="pb-1">Laporan Hutang</h3>
          <div className="table-wrap">
            <table>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Total</th>
                  <th>Return Date</th>
                </tr>
              </thead>
              <tbody>
                <Loans />
              </tbody>
            </table>
          </div>
        </div>
      </div>
      {/* <div className="h-50v d-grid gap-3">
        <div className="wrap-b h-100 overflow-hidden p-3">
          <h3 className="pb-1">Laporan Saham</h3>
          <div className="table-wrap">
            <table>
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Modal</th>
                  <th>Pengeluaran</th>
                  <th>Pendapatan</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>10-10-2020</td>
                  <td>100000</td>
                  <td>{senin.Pengeluaran}</td>
                  <td>{senin.Pendapatan}</td>
                  <td>Rp. 123000</td>
                </tr>
                <tr>
                  <td>11-10-2020</td>
                  <td>123000</td>
                  <td>{selasa.Pengeluaran}</td>
                  <td>{selasa.Pendapatan}</td>
                  <td>Rp. 90000</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div> */}
    </div>
  )
}

export default ChartFull
