import React, { useEffect, useState } from 'react';
import { 
  BrowserRouter as Router,
  Redirect,
  Route, 
  Switch,
 } from 'react-router-dom';
import Bank from '../body/bank';
import ListUser from '../body/listUser';
import EditUser from '../body/editUser';
import Loan from '../body/loan';
import OtherP from '../body/otherP';
import Product from '../body/product';
import Profile from '../body/profile';
import Chart from '../body/chartFull';
import Navbar from "./navbar";
import SideBar from './sidebar';

const Template = ({ role, setRole }) => {

  const [red, setRed] = useState(false);
  const [size, setSize] = useState(120);

  const onResize = () => {
    if (window.innerWidth <= 768) {
      setSize(100)
    } else {
      setSize(120)
    }
  }

  useEffect(() => {
    onResize();
    window.addEventListener('resize', onResize);
    return () => window.removeEventListener('resize', onResize);
  }, [window]);

  if (red) {
    return <Redirect to="/home" />
  }

  return (
    <Router basename="/">
      <div className="container h-100v">
        <div className="row h-100">
          <Navbar setRed={setRed} user={role?.role[0]} sized={window.innerWidth} />
          <SideBar role={role} />
          <div className="col p-md-4 overflow-auto h-100 d-flex align-items-center flex-direction-column" style={{ paddingBottom: size, paddingTop: size }}>
            <Switch>
              <Route exact path="/" render={() => <Product role={role} />} />
              <Route exact path="/list-user" component={ListUser} />
              <Route exact path="/other-product" render={() => <OtherP role={role} />} />
              <Route exact path="/debt" render={() => <Loan role={role} />} />
              <Route exact path="/savings" render={() => <Bank role={role} />} />
              <Route exact path="/chart" component={Chart} />
              <Route exact path="/profile" render={() => <Profile setRole={setRole} />} />
              <Route exact path="/edit-user" component={EditUser} />
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  )
}

export default Template;
