import axios from 'axios';
import React, { useEffect, useState } from 'react'
import SkeletonLoader from "tiny-skeleton-loader-react";

const UserData = () => {

  const [userDet, setUser] = useState();
  const [keyword, setKeyword] = useState([]);
  const [load, setLoad] = useState(true);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }
  
  const token = getToken();

  useEffect(() => {
    axios.get(
      `https://coperationv2.herokuapp.com/api/users?keyword=${keyword?.keyword}`,
      {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      setUser(data.data.success)
      setLoad(false);
    })
  }, [token, keyword]);

  const myFunction = (e) => {
    var popup = document.getElementById('pop-'+e);
    popup.classList.toggle("show");
  }

  const deleteUser = async (e) => {
    var ty = await axios.delete(
      `https://coperationv2.herokuapp.com/api/user/delete/${e.target.parentElement.id.split('-')[1]}`,
      {
        headers: {
          Authorization: 'Bearer ' + token
        }
      }
    ).then(data => data).catch(data => {return {error: data}})

    if (ty?.error === undefined) {
    }
    setKeyword([...keyword])
  }

  if (!load) {
    return userDet?.map((data, index) => (
      <div className="role-item d-flex w-100 cursor-pointer" onClick={() => myFunction(data?.id)} key={index}>
        <span className="popuptext" id={'pop-'+data?.id}>
          <i className="material-icons" onClick={deleteUser}>delete</i>
        </span>
        <div className="col-3 d-flex align-items-center">
          <div className="img-profile position-relative" style={{ width: '100%', height: 0, paddingTop: '100%' }}>
            <img src={`https://coperationv2.herokuapp.com/img_profile/${data?.image}`} alt="" style={{ top: 0 }} className="position-absolute" />
          </div>
        </div>
        <div className="col-9 d-flex flex-direction-column justify-content-center text-overflow-ellipsis">
          <h4>{data?.name}</h4>
          <p style={{ fontSize: '12px' }}>{data?.email}</p>
        </div>
      </div>
    ))
  } else {
    return (
      <div className="role-item-load d-flex w-100" style={{ height: '100%' }}>
        <div className="col-3 d-flex align-items-center">
          <SkeletonLoader circle="true" width="100%" height="0" style={{paddingTop: '100%'}} />
        </div>
        <div className="col-9 d-flex flex-direction-column justify-content-space-around" style={{ height: '100%' }}>
          <SkeletonLoader width="35%" />
          <SkeletonLoader width="calc(100% - 15px)" />
        </div>
      </div>
    )
  }
}

export default UserData
