import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router';
import OtherPData from '../data/otherPData';
import Modal from './Modal';
import ModalPP from './modalPP';
import Popup from './Popup';

const OtherP = ({ role }) => {

  const [search, setS] = useState();
  const [get, setGet] = useState({keyword: ''});
  const [product, addProduct] = useState(false);
  const [datas, setDatas] = useState({});
  const [del, setdel] = useState([]);
  const [view, setView] = useState('table');
  const [use, setModal] = useState(false);

  const history = useHistory();

  let require = {
    image: 'image',
    user_id: '',
    name_barang: '',
    jenis: '',
    stock: 0,
    harga: 0,
  }

  const searchH = e => {
    return setTimeout(() => {
      setGet({
        keyword: e.value
      })
    }, 500);
  }

  const serG = e => {
    e.preventDefault();
    clearInterval(search)
    setS(undefined);
    setS(searchH({
      value: e.target.value
    }));
  }

  useEffect(() => {
    if (role?.data?.[0] === undefined) {
      return history.push('/profile');
    }
  }, [role]);

  return (
    <div className="wrapi d-flex flex-direction-column w-100">
      <Popup del={del} keyword={setGet} setdel={setdel} href="barang-penjual" />
      <Modal addProduct={addProduct} product={product} keyword={setGet} href="barang-penjual" require={require} name="Other Product" />
      <ModalPP setModal={setModal} use={use} keyword={setGet} href="barang-penjual" data={datas} />
      <div className="wrap-he d-flex align-items-center justify-content-space-between">
        <div className="wrap-ty d-flex align-items-center gap-2">
          <h3>Other Products</h3>
          <i className="material-icons p-05 cursor-pointer" style={{ backgroundColor: 'rgb(240, 240, 240)', borderRadius: '25px', color: 'rgb(120, 120, 120)' }} onClick={() => setView('table')}>
            view_list
          </i>
          <i className="material-icons p-05 cursor-pointer" style={{ backgroundColor: 'rgb(240, 240, 240)', borderRadius: '25px', color: 'rgb(120, 120, 120)' }} onClick={() => setView('grid')}>
            view_module
          </i>
        </div>
        <div className="chart-se">
          <div className="role-se d-flex align-items-center">
            <i className="material-icons">search</i>
            <input type="text" onChange={serG} />
          </div>
        </div>
      </div>
      <div className="wrap-b">
        <OtherPData keyword={get} addProduct={addProduct} setModal={setModal} setDatas={setDatas} setdel={setdel} view={view} />
      </div>
    </div>
  )
}

export default OtherP
