import axios from "axios";

const TransaksiPut = async ({ data, id }) => {

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  try {
    id.map(async (dat, index) => {
      var ty = dat.split('-');
      const as = await axios.put(
        `https://coperationv2.herokuapp.com/api/barang-${ty[0]}/stock/${ty[1]}`,
        {
          stock: (data[index]?.jumlah_asli - data[index]?.jumlah),
        },
        {
          headers: {
            Authorization: 'Bearer ' + getToken()
          }
        }
      ).then(data => data).catch(data => data);
      console.log(as);
    })
    return true;
  } catch (error) {
    console.log(error)
    return false;
  }
}

export default TransaksiPut
