import axios from 'axios';
import React from 'react'
import { useHistory } from 'react-router';

const EditUser = () => {

  const history = useHistory();

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }
  
  const token = getToken();

  const submitHandle = async (e) => {
    e.preventDefault();
    const yu = await axios.put(
      `https://coperationv2.herokuapp.com/api/user/update-profile`,
      {
        name: e.target[0].value,
        email: e.target[1].value
      },
      {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => data)
    .catch(data => {
      return {
        error: data
      }
    })

    if (yu?.error === undefined) {
      return history.goBack();
    }
  }

  return (
    <div className="profile-wrap d-flex flex-direction-column">
      <div className="profile-body position-relative d-flex flex-direction-column">
        <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
          <h3>Edit User</h3>
        </div>
        <form action="#" className="form-wrap d-flex flex-direction-column" onSubmit={submitHandle}>
          <div className="form-row d-flex flex-direction-column">
            <div className="form-col">
              <label htmlFor="name">
                <h4>Name</h4>
              </label>
            </div>
            <div className="form-col">
              <input type="text" name="name" className="form-input" />
            </div>
          </div>
          <div className="form-row d-flex flex-direction-column">
            <div className="form-col">
              <label htmlFor="email">
                <h4>Email</h4>
              </label>
            </div>
            <div className="form-col">
              <input type="text" name="email" className="form-input" />
            </div>
          </div>
          <button className="save-form align-self-center">
            <h4>Save Changes</h4>
          </button>
        </form>
      </div>
    </div>
  )
}

export default EditUser
