import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import UseAnimations from 'react-useanimations';
import trash2 from 'react-useanimations/lib/trash2'

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const Popup = ({ del, keyword, setdel, href }) => {

  const popup = useRef();
  const [right, setRight] = useState('0');
  // const [load, setLoad] = useState(false);

  useEffect(() => {
    setRight('calc(50% - ' + popup.current.clientWidth / 2 + 'px)')
  }, [popup]);

  const trashHandle = async e => {
    e.preventDefault();
    // setLoad(true);
    const token = getToken();
    let mess = {}

    for (let index = 0; index < del?.length; index++) {
      await axios.delete(
        `https://coperationv2.herokuapp.com/api/${href}/${parseInt(del?.[index].id)}`,
        {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          }
        }
      ).then(data => {
        mess.success = data
      }).catch(data => {
        mess.error = data
      })
    }

    if (mess?.error === undefined) {
      setdel([])
      keyword(asd => {return {...asd}})
      gy()
      // setLoad(false)
    }
  }

  const gy = () => {
    for (let index = 0; index < del?.length; index++) {
      try {
        del[index].classList.remove('tr-hover'); 
        del[index].firstChild.firstChild.classList.add('d-none')
      } catch (error) {
        
      }
    }
  }

  useEffect(() => {
    if (del?.length !== 0 && del !== undefined) {
      popup.current.classList.remove('reanimate4');
      popup.current.classList.add('animate4');
    } else {
      popup.current.classList.remove('animate4');
      popup.current.classList.add('reanimate4');
    }
  }, [del]);

  return (
    <div ref={popup} className="position-fixed" style={{ top: 0, right: right, zIndex: 2 }}>
      <div className="popup">
        <UseAnimations animation={trash2} onClick={trashHandle} />
      </div>
    </div>
  )
}

export default Popup
