import React, { useEffect, useState } from 'react'
import Loader from 'react-loader-spinner'

const LoaderData = () => {

  const [size, setSize] = useState(130);

  useEffect(() => {
    if (window.innerWidth <= 768) {
      setSize(70);
    } else {
      setSize(130);
    }
  }, [window.innerWidth]);

  return (
    <Loader
      type="ThreeDots"
      color="rgba(0,90,133,1)"
      height={size}
      width={size}
      style={{ padding: '20px' }}
    />
  )
}

export default LoaderData
