import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import Loader from 'react-loader-spinner';

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const ModalC = ({ use, setModal }) => {

  const modal = useRef();
  const buttonR = useRef();

  const [load, setLoad] = useState(false);
  const [password, setPassword] = useState('');

  const modalClose = (e) => {
    if (e.target.children[0] === modal.current.children[0]) {
      setModal(false)
    }
  }

  useEffect(() => {
    if (use) {
      modal.current.style.display = 'flex'
    } else {
      modal.current.style.display = null
    }
  }, [modal, use]);

  const SPass = (e) => {
    e.preventDefault();
    if (e.target.value !== password) {
      e.target.classList.add('border-red');
      return false;
    }
    e.target.classList.remove('border-red');
  }

  useEffect(() => {
    if (load) {
      modal.current.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      modal.current.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  const handleSubmit = async e => {
    e.preventDefault();
    setLoad(true);
    const token = getToken();
    const datas = new FormData(e.target);
    const yu = await axios.post(
      `https://coperationv2.herokuapp.com/api/change-password`,
      datas,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      return {
        'success': data
      }
    }).catch(data => {
      return {
        'error': data
      }
    })

    if (yu?.error === undefined && yu !== undefined) {
      setLoad(false);
      setModal(false)
    } else {
      setLoad(false)
    }
  }

  return (
    <div className="modal justify-content-center align-items-start" style={{ padding: '100px 0' }} ref={modal} onClick={modalClose}>

      <div className="modal-content col-5 col-md-7 col-sm-10 wrap-b position-relative d-flex flex-direction-column p-5 overflow-unset">
        <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
          <h3>Change Password</h3>
        </div>
        <div className="card-modal" style={{ zIndex: 1, left: '25%', right: '25%' }}>
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
            className="justify-content-center align-items-center d-flex"
            style={{
              position: 'absolute',
              top: '0',
              left: '0',
              bottom: '0',
              right: '0',
            }}
          />
        </div>
        <form action="#" onSubmit={handleSubmit} encType="multipart/form-data" className="form-wrap d-flex flex-direction-column">
          <div className="modal-body gap-2 d-flex align-items-center justify-content-center flex-direction-column p-3">
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="old_password">Old Password</label>
              <input className="w-100 input-modal background-gray600" type="password" name="old_password" />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="password">New Password</label>
              <input className="w-100 input-modal background-gray600" type="password" name="new_password" onInput={e => setPassword(e.target.value)} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="c_password">Re New password</label>
              <input className="w-100 input-modal background-gray600" type="password" name="new_confirm_password" onInput={SPass} />
            </div>
          </div>
          <div className="d-grid gap-2" style={{ gridTemplateColumns: '1fr 1fr' }}>
            <button ref={buttonR} className="submitF cursor-pointer background-red align-self-center" type="reset" onClick={() => setModal(false)}>
              <h4>Cancel</h4>
            </button>
            <button className="submitF cursor-pointer background-blues300 align-self-center">
              <h4>Add</h4>
            </button>
          </div>
        </form>
      </div>

    </div>
  )
}

export default ModalC
