import React, { useEffect, useState } from 'react'
import Loader from 'react-loader-spinner'

const LoaderSP = () => {

  const [r, setR] = useState(300)

  useEffect(() => {
    if (window.innerWidth <= 768) {
      setR(80)
    } else {
      setR(300)
    }
  }, [window.innerWidth]);

  return (
    <div className="d-flex align-items-center justify-content-center h-100 w-100 position-absolute" style={{ top: 0, zIndex: 4, overflow: 'hidden', backgroundColor: 'rgba(255, 255, 255, 1)' }}>
      <Loader
        type="Watch"
        color="rgba(0,90,133,1)"
        height={r}
        width={r}
      />
    </div>
  )
}

export default LoaderSP
